import { useState, ChangeEvent } from "react";
import { Link } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { Web3ReactProvider, useWeb3React } from '@web3-react/core';
//import { Web3Provider } from "@ethersproject/providers";
////import { WalletLinkConnector } from "@web3-react/walletlink-connector";
import { WalletConnectConnector } from "@web3-react/walletconnect-connector";
///import { InjectedConnector } from "@web3-react/injected-connector";
//import { ethers } from "ethers";

const WalletConnectApp = () => {

  //#region State
  const [currentAccount, setCurrentAccount] = useState<string | undefined>();
  const [balance, setBalance] = useState<number | undefined>();
  //const [chainId, setChainId] = useState<number | undefined>();
  const [chainName, setChainName] = useState<string | undefined>();
  //#endregion

  //#region
  const WalletConnect = new WalletConnectConnector({
      //rpcUrl: `https://mainnet.infura.io/v3/${process.env.INFURA_KEY}`,
      rpc: { [4]: "https://rinkeby.infura.io/v3/" },
      chainId: 4,
      bridge: "https://bridge.walletconnect.org",
      qrcode: true,
  });

  //const { active, chainId, account } = useWeb3React();
  const { activate, deactivate, active, chainId, account } = useWeb3React();
  //#endregion
  
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  return (
    <>
      <button onClick={() => { activate(WalletConnect) }} className="btn btn-success">
        {t('walletconnect.button_connect')}
      </button>
      <h5 className="mt-3">{t('walletconnect.my_account')}:</h5>
      <p className="text-break">Connection Status: {active}</p>
      <p className="text-break">{t('walletconnect.address')}: {currentAccount}</p>
      <p className="text-break">{t('walletconnect.eth_balance')}: {balance}</p>
      <p className="text-break">{t('walletconnect.chain_id')}: {chainId}</p>
      <p className="text-break">{t('walletconnect.chain_name')}: {chainName}</p>
    </>
  );
}

export default WalletConnectApp